import styled from 'styled-components'
import {DefaultButtonStyles, TextInputStyle} from '../../app-styled-components'
import {Field} from 'react-final-form'

export let FormBox = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
`

export let InputText = styled(Field)`
${TextInputStyle}
margin-top: 30px;
`

export let SubmitButton = styled.button`
${DefaultButtonStyles}
margin-top: 60px;
margin-bottom: 60px;
`

export let LoadButton = styled.div`
${DefaultButtonStyles}
margin-top: 60px;
margin-bottom: 60px;
`

export {}