import styled from 'styled-components'

export let Categories = styled.div`
    position: fixed;
    width: 100%;
    z-index: 10;
    background: linear-gradient(90deg, #6D4BCE 0%, #4398B6 100%);
    height:  ${props => props.active ? '0' : '118px'};
    padding: ${props => props.active ? '0' : '8px 0'};
    transition: 0.5s;
`

export let CategoriesLine = styled.div`
    height:  ${props => props.active ? '0' : '59px'};
    opacity:  ${props => props.active ? '0' : '1'};
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 0 22px;
    transition: 0.25s;
    
   ::-webkit-scrollbar {
    height: 0px;
    }

`

export let MarginBlock = styled.p`
width: 8px;
height: 8px;
p {
width: 8px;
height: 8px;
}
`

export let DefaultButton = styled.div`
height: 43px;
background: radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%);
box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
border-radius: 47px;
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 52px;
letter-spacing: -0.32px;
text-transform: uppercase;
color: white;
cursor: pointer;
width: min-content;
padding: 0 14px;
display: flex;
align-items: center;
margin: 8px 14px 8px 0;
user-select:none;
transition: 0.25s;
 -webkit-tap-highlight-color: rgba(0,0,0,0); 
 -webkit-tap-highlight-color: transparent;
`
export let Button = styled(DefaultButton)`
color: ${props => props.active ? 'rgba(0, 0, 0, 0.8)' : 'white'};
background: ${props => props.active ? 'white' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let YellowButton = styled(DefaultButton)`
background: ${props => props.active ? '#BFB439' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let GreenButton = styled(DefaultButton)`
background: ${props => props.active ? '#36B782' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let PurpleButton = styled(DefaultButton)`
background: ${props => props.active ? '#8B3BBE' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let RedButton = styled(DefaultButton)`
background: ${props => props.active ? '#BF3339' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`