import React, {FC, useState} from 'react'
import {connect} from 'react-redux'
import {AppStateType} from '../../../store/store'
import {Card, HeadlineContainer, Image, Info, Label, Like, TextInfo} from './style'
import {PasteSmallPlug} from '../../../services/paste_small_plug'
import likeIcon from '../../../images/like.svg'



///////////// types for props
type PropsType = {
    local: string[]
}

///// for mock
function randomInteger(min: number, max: number) {
    let rand = min - 0.5 + Math.random() * (max - min + 1)
    return Math.round(rand)
}
let colors = [undefined, '#BFB439', '#35B881', '#8B3BBE', '#BF3339']
/////



let SmallAdCard: FC<PropsType> = ({
                                      local
    }) => {
    ///// for save plug image
    const [image, setImage] = useState(PasteSmallPlug())
    const [category, setCategory] = useState(randomInteger(1, 4))
    


    return <Card>
        <Image>
            <img src={image} alt={local[0]}/>
        </Image>
        <Info>
            <HeadlineContainer>
                <p>Знайшов годинник крутий</p>
            </HeadlineContainer>
            <Label color={colors[category]}>{local[category]}</Label>
            <TextInfo>Вася<br/>11.06.21<span>12:00</span></TextInfo>
        </Info>
        <Like src={likeIcon} />
    </Card>
}

///////////// create props for component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.small_ad_card
    }
}


export default connect(mapStateToProps, {})(SmallAdCard)
