import {Field} from 'react-final-form'
import React, {FC, useRef, useState} from 'react'
import styled from 'styled-components'
import avatarIcon from '../../../images/avatar.png'
import {DefaultButtonStyles} from '../../../app-styled-components'
import {AppStateType} from '../../../store/store'
import {connect} from 'react-redux'


type PropsType = {
    local: Array<string>
}

const UploadAvatar: FC<PropsType> = ({local}) => {
    
    ///// uploads images
    const [stateImg, setStateImg]: any = useState({
        files: []
    })

    ///// ref for upload input
    const fileInput: any = useRef(null)
    ///// function make click to input when upload button clicked
    const clickToUpload = () => {
        fileInput.current.click()
    }

    return <ImgFieldContainer>
        <ImgField>
        <Field name='avatar'>
            {({input: {value, onChange, ...input}}) => {
                const handleChange = ({target}: React.ChangeEvent<HTMLInputElement>) => {
                    onChange(target.files)
                    let imgArray = []
                    if (target.files !== null) {
                        for (let i = 0; i < target.files.length; i++) {
                            imgArray.push(URL.createObjectURL(target.files[i]))
                        }
                    }
                    
                    setStateImg({
                        files: imgArray
                    })
                }
                return <input {...input} type="file" ref={fileInput} onChange={handleChange}/>
            }}
        </Field>
        </ImgField>
        <ImageContainer>
            {stateImg.files.length === 0
            ? <Photo src={avatarIcon} alt={local[0]} />
            : <Photo src={stateImg.files?.[0]} alt={local[0]} />}
            <DownloadPhotoButton onClick={()=> {clickToUpload()}}>{local[1]}</DownloadPhotoButton>
        </ImageContainer>
    </ImgFieldContainer>
}


///////////// create props for App component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.auth_page,
    }
}

export default connect(mapStateToProps, {})(UploadAvatar)


let DownloadPhotoButton = styled.div`
${DefaultButtonStyles}
margin-top: 67px;
`

let Photo = styled.img`
height: 127px;
width: 127px;
box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.21);
border-radius: 100%;
margin-top: 86px;
`

let ImgFieldContainer = styled.div`
width: 100%;
`

let ImgField = styled.div`
display: none;
`

let ImageContainer = styled.div`
display: flex;
flex-direction: column;
align-items: center;
`