import React, {FC} from 'react'
import {
    Button,
    Categories,
    CategoriesLine,
    GreenButton,
    MarginBlock,
    PurpleButton,
    RedButton,
    YellowButton
} from './style'

///////////// types for props
type PropsType = {
    SelectAllMainCategories: () => void
    SelectAllSecondCategories: () => void
    ChangeMainCategory: (categoryId: number) => void
    ChangeSecondCategory: (categoryId: number) => void
    
    showCategories: boolean
    mainCategoriesState: Array<boolean>
    secondCategoriesState: Array<boolean>
    local: Array<string>
}


let ChooseCategory: FC<PropsType> = ({
                                         showCategories,
                                         mainCategoriesState,
                                         secondCategoriesState,
                                         SelectAllMainCategories,
                                         ChangeMainCategory,
                                         local,
                                         SelectAllSecondCategories,
                                         ChangeSecondCategory
}) => {


    {/*@ts-ignore*/}
    return <Categories active={showCategories}>
            {/*@ts-ignore*/}
        <CategoriesLine active={showCategories}>
            {/*@ts-ignore*/}
            <Button onClick={()=>{SelectAllMainCategories()}} active={mainCategoriesState[0]}>{local[0]}</Button>
            {/*@ts-ignore*/}
            <YellowButton onClick={()=>{ChangeMainCategory(1)}} active={mainCategoriesState[1]}>{local[1]}</YellowButton>
            {/*@ts-ignore*/}
            <GreenButton onClick={()=>{ChangeMainCategory(2)}} active={mainCategoriesState[2]}>{local[2]}</GreenButton>
            {/*@ts-ignore*/}
            <PurpleButton onClick={()=>{ChangeMainCategory(3)}} active={mainCategoriesState[3]}>{local[3]}</PurpleButton>
            {/*@ts-ignore*/}
            <RedButton onClick={()=>{ChangeMainCategory(4)}} active={mainCategoriesState[4]}>{local[4]}</RedButton>
            <MarginBlock><p/></MarginBlock>
        </CategoriesLine>
            {/*@ts-ignore*/}
        <CategoriesLine active={showCategories}>
            {/*@ts-ignore*/}
            <Button onClick={()=>{SelectAllSecondCategories()}} active={secondCategoriesState[0]}>{local[0]}</Button>
            {/*@ts-ignore*/}
            <Button onClick={()=>{ChangeSecondCategory(1)}} active={secondCategoriesState[1]}>{local[5]}</Button>
            {/*@ts-ignore*/}
            <Button onClick={()=>{ChangeSecondCategory(2)}} active={secondCategoriesState[2]}>{local[6]}</Button>
            {/*@ts-ignore*/}
            <Button onClick={()=>{ChangeSecondCategory(3)}} active={secondCategoriesState[3]}>{local[7]}</Button>
            {/*@ts-ignore*/}
            <Button onClick={()=>{ChangeSecondCategory(4)}} active={secondCategoriesState[4]}>{local[8]}</Button>
            <MarginBlock><p/></MarginBlock>
        </CategoriesLine>
    </Categories>
}


export default ChooseCategory