import plug1 from '../images/plugs/plug1.jpg'
import plug2 from '../images/plugs/plug2.jpg'
import plug3 from '../images/plugs/plug3.jpg'
import plug4 from '../images/plugs/plug4.jpg'
import plug5 from '../images/plugs/plug5.jpg'
import plug6 from '../images/plugs/plug6.jpg'
import plug7 from '../images/plugs/plug7.jpg'

let plugs = [
    plug1,
    plug2,
    plug3,
    plug4,
    plug5,
    plug6,
    plug7
]

export let PasteSmallPlug = () => {
    return plugs[Math.floor(Math.random() * plugs.length)]
}