import styled from 'styled-components'

export let Card = styled.div`
width: calc(100% - 44px);
height: 152px;
background: radial-gradient(97.57% 210.75% at 0.9% 2.98%,rgba(0,0,0,0.4) 0%,rgba(0,0,0,0.4) 99.99%);
border-radius: 47px;
align-self: center;
margin-bottom: 14px;
position: relative;
display: flex;
min-width: 329px;
`
export let Like = styled.img`
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
     -webkit-tap-highlight-color: rgba(0,0,0,0); 
 -webkit-tap-highlight-color: transparent;
`

export let Image = styled.div`
height: 152px;
width: 152px;

img {
width: inherit;
height: inherit;
border-radius: 47px 0 0 47px;
}
`

export let Info = styled.div`
width: 100%;
`

export let HeadlineContainer = styled.div`
width: calc(100% - 80px);
min-width: 100px;
overflow-y: hidden;
height: 36px;
margin-left: 15px;
margin-top: 19px;
display: flex;
align-items: flex-start;

p {
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 18px;
letter-spacing: 0.68px;
text-transform: uppercase;
color: #FFFFFF;
    margin: 0;
}
`

export let Label = styled.div`
margin-left: 15px;
margin-top: 12px;
background: ${props => props.color};
box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.21);
border-radius: 49px;
width: 132px;
height: 26px;
display: flex;
align-items: center;
justify-content: center;
color: white;
font-style: normal;
font-weight: 800;
font-size: 13px;
letter-spacing: -0.32px;
text-transform: uppercase;
`

export let TextInfo = styled.p`
margin: 13px 0 0 15px;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 13px;
letter-spacing: -0.32px;
text-transform: capitalize;

color: rgba(255, 255, 255, 0.7);

span {
margin-left: 5px;
}
`

