/*
File contains functions for validation fields in forms.
 */

export const required = (value: any) => (value ? undefined : "Обов'язкове поле")
export const numberQuantity = (number: any) => (value: any) => (
    value.length === number ? undefined : `Не верный формат`
)


export const minLength = (number: any) => (value: any) => {
    if (value) {
        return value.length >= number ? undefined : `Минимальное количество символов ${number}`
    }
}
    


export const maxLength = (number: any) => (value: any) => {
    if (value) {
       return  value.length <= number ? undefined : `Максимальное количество символов ${number}`
    }
}


export const composeValidators = (...validators: any) => (value: any) =>
    validators.reduce((error: any, validator: any) => error || validator(value), undefined)