import './app-styles.css'
import {Container, FullScreenBlock} from './app-styled-components'
import React, {FC, useEffect, useState} from 'react'
import {developerFun} from './services/developer_functions'
import {getCookie} from './services/cookies_functions'
import {AppStateType} from './store/store'
import {connect, ConnectedProps} from 'react-redux'
import {SetUser} from './store/auth-reducer'
import {BrowserRouter, Route} from 'react-router-dom'
import PrivateRoute from './HOC/private-route'
import MainContainer from './components/main-page/main-container'
import AuthContainer from './components/auth-page/auth-container'
import Instruction from './components/instruction-page/instruction'
import Preloader from './components/common-components/preloader/preloader'

type PropsType = ConnectedProps<typeof connector>

let App: FC<PropsType> = ({ isRegistered, SetUser, local }) => {
    
    useEffect(()=>{
        ///// cookies
        if (getCookie('registered') === 'true' && isRegistered === null) {
            let userData = {
                isRegistered: true,
                name: getCookie('user'),
                idUser: getCookie('idUser'),
                phone: getCookie('phone'),
                avatar: getCookie('avatar')
            }
            SetUser(userData)
        } else {
            let userData = {
                isRegistered: false,
                name: null,
                idUser: null,
                phone: null,
                avatar: null
            }
            SetUser(userData)
        }
    },[])
    
    ///// developer delete cookies button CTRL and ALT get state
    developerFun()
    
    ///// for dynamic height
    const [height, setHeight] = useState(window.innerHeight)
    // @ts-ignore
    useEffect(() => {
        function handleResize() {
            setHeight(window.innerHeight)
        }

        window.addEventListener('resize', handleResize)
        return (_: any) => {
            window.removeEventListener('resize', handleResize)
        }
    })

    // @ts-ignore
    return <BrowserRouter><Container height={height}>
        {isRegistered === null
        ? <Preloader full />
        : <>
                <Route path='/login' exact render={() =><AuthContainer />}/>
                <Route path='/instruction' exact render={() =><Instruction />}/>
                <Route path='/' exact render={()=> <PrivateRoute component={MainContainer} auth={isRegistered}/>}/>
            </>} 
            
    </Container>
        {/*@ts-ignore*/}
        <FullScreenBlock height={height}>
            <h2>{local[0]}<br/>{local[1]}</h2>
        </FullScreenBlock>
    </BrowserRouter>
}

///////////// create props for App component
let mapStateToProps = (state: AppStateType) => {
    return {
        isRegistered: state.AuthReducer.isRegistered,
        local: state.LocalizationReducer.langData.common
    }
}

const connector = connect(mapStateToProps, { SetUser })
export default connector(App)
