import React, {FC} from 'react'
import { Redirect } from 'react-router-dom'
import {AppStateType} from '../../store/store'
import {connect, ConnectedProps} from 'react-redux'
import Main from './main'
import {getCookie} from '../../services/cookies_functions'

type PropsType = ConnectedProps<typeof connector>

let MainContainer: FC<PropsType> = ({}) => {

    if (getCookie('isReadInstruction') !== 'true') {
        return <Main/>
    } else {
        return <Redirect to={'/instruction'} />
    }
    
}

///////////// create props for App component
let mapStateToProps = (state: AppStateType) => {
    return {
    }
}

const connector = connect(mapStateToProps, {})
export default connector(MainContainer)
