import React, {FC, useEffect, useState} from 'react'
import ChooseCategoryContainer from '../common-components/choose-category/choose-category-container'
import {Ads, Container} from './style'
import {IsScroll} from '../../services/scroll_listener'
import Navigation from '../common-components/navigation/navigation'
import SmallAdCard from '../common-components/small-ad-card/small-ad-card'


///////////// types for props
type PropsType = {
}

let Main: FC<PropsType> = ({}) => {
    
    ///// state for listen scroll up or down
    const [scrollPosition, setScrollPosition] = useState([0,0])
    const [upOrDownScroll, setScrollState] = useState([false, false])
    


    return <Container onScroll={(e: any)=>{IsScroll(e, upOrDownScroll, setScrollState, scrollPosition, setScrollPosition)}}>
    <ChooseCategoryContainer upOrDownScroll={upOrDownScroll} />
    <Ads>
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
        <SmallAdCard />
    </Ads>
        <Navigation />
    </Container>
}


export default Main