// @ts-ignore
import React from 'react'
import styled from 'styled-components'


// @ts-ignore
const InputField = ({input, meta, ...props}) => {

    let hasError = meta.touched && meta.error

    return <FieldContainer>
        <input
            {...input}
            {...props}
        />
        {hasError && <ErrorSpan>{meta.error}</ErrorSpan>}
    </FieldContainer>
}

export default InputField


let FieldContainer = styled.div`
    width: inherit;
    height: inherit;
    position: relative;
   display: flex;
    justify-content: center;
`

let ErrorSpan = styled.span`
position: absolute;
    color: red;
    bottom: -20px;
`