import {AxiosResponse} from 'axios'
import {ThunkAction} from 'redux-thunk'
import {AppStateType} from './store'
import MainApi from '../api/main-api'
import {AddUserDataCookie} from '../services/cookies_functions'
import {AuthDataType, InitialStateAndUserDataType} from '../types/types'
import {generatorId} from '../services/generator_id'

///////////// Const for actioncreators
const SET_AUTH_USER_DATA = 'SET_AUTH_USER_DATA'
const SET_AUTH_USER_DATA_AND_REDIRECT = 'SET_AUTH_USER_DATA_AND_REDIRECT'
const TOGGLE_LOADER_SUBMIT = 'TOGGLE_LOADER_SUBMIT'
const CLEAN_STATE = 'CLEAN_STATE'

///////////// Initial state
let initialState: InitialStateAndUserDataType = {
    isRegistered: null,
    name: null,
    phone: null,
    avatar: null,
    idUser: null,
    isLoadNewUser: false,
    isRedirectToMainPage: false
}

///////////// Reducer
const AuthReducer = (state = initialState, action: ActionsTypes): InitialStateAndUserDataType => {
    switch (action.type) {
        case SET_AUTH_USER_DATA: {
            return {
                ...state,
                isRegistered: action.userData.isRegistered,
                name: action.userData.name,
                idUser: action.userData.idUser,
                phone: action.userData.phone,
                avatar: action.userData.avatar
            }
        }
        case TOGGLE_LOADER_SUBMIT:{
            
            let newValue = true
            if (state.isLoadNewUser) {
                newValue = false
            }
            
            return {
                ...state,
                isLoadNewUser: newValue
            }
        }
        case SET_AUTH_USER_DATA_AND_REDIRECT: {
            return {
                ...state,
                isRegistered: action.userData.isRegistered,
                name: action.userData.name,
                idUser: action.userData.idUser,
                phone: action.userData.phone,
                avatar: action.userData.avatar,
                isLoadNewUser: false,
                isRedirectToMainPage: true
            }
        }
        case CLEAN_STATE: {
            
            return {
                ...state,
                isLoadNewUser: false,
                isRedirectToMainPage: false
            }
        }
        default: {
            return state
        }
    }

}

///////////// Action creators
// actions types
type ActionsTypes = 
    SetUserType |
    ToggleLoadSubmitType |
    SetUserAndRedirectType |
    CleanStateType
//

type CleanStateType = {
    type: typeof CLEAN_STATE
}

export let CleanState = ():CleanStateType => ({type: CLEAN_STATE})

type ToggleLoadSubmitType = {
    type: typeof TOGGLE_LOADER_SUBMIT
}

export let ToggleLoadSubmit = (): ToggleLoadSubmitType => ({type: TOGGLE_LOADER_SUBMIT})


type SetUserType = {
    type: typeof SET_AUTH_USER_DATA
    userData: UserDataType
}
type UserDataType = {
    isRegistered: boolean
    name: string | null
    idUser: string | null
    phone: string | null
    avatar: string | null
}
export let SetUser = (userData: UserDataType): SetUserType => ({
    type: SET_AUTH_USER_DATA,
    userData: userData
})

type SetUserAndRedirectType = {
    type: typeof SET_AUTH_USER_DATA_AND_REDIRECT
    userData: UserDataType
}
let SetUserAndRedirect = (userData: UserDataType): SetUserAndRedirectType => ({type: SET_AUTH_USER_DATA_AND_REDIRECT, userData})

///////////// Thanks
// types for thunks
type ThunkActions = ThunkAction<Promise<void>, AppStateType, unknown, ActionsTypes>
//
export let AddNewUserThunk = (formData: AuthDataType): ThunkActions => async (dispatch) => {
    
    const userId: string = generatorId()
    type UserDataType = {
        isRegistered: boolean
        name: string
        avatar: string | null
        phone: string | null
        idUser: string
    }
    
    let payload = new FormData()
    let userData: UserDataType = {
        isRegistered: true,
        name: formData.name,
        avatar: null,
        phone: null,
        idUser: userId
    }

    payload.append('name', formData.name)
    if (formData.avatar) {
        payload.append('avatar', formData.avatar[0])
    }
    if (formData.phone && formData.phone !== '') {
        payload.append('phone', formData.phone)
        userData.phone = formData.phone
    }
    payload.append('idUser', userId)
    

    let response: AxiosResponse<any> = await MainApi.AddNewUser(payload)

    if (response.status === 201) {
        userData.avatar = response.data.avatar
        AddUserDataCookie(userData)
        dispatch(SetUserAndRedirect(userData))
    }
    else {
        console.log('AddNewUserThunk error.')
    }
}
/////////////

export default AuthReducer