export let IsScroll = (
    e: any, 
    upOrDownScroll: Array<boolean>, 
    setScrollState: (value: Array<boolean>) => void, 
    scrollPosition: Array<number>,
    setScrollPosition: (value: Array<number>) => void,
) => {

    if (!upOrDownScroll[0] && !upOrDownScroll[1]) {

        ///// set state when user first time scroll to down
        if (scrollPosition[1] < e.target.scrollTop) {
            setScrollPosition([scrollPosition[1], e.target.scrollTop])
            setScrollState([true, false])
        }

    } else if (upOrDownScroll[0]) {

        ///// set state when user scroll to up
        if (scrollPosition[1] > e.target.scrollTop) {
            setScrollPosition([scrollPosition[1], e.target.scrollTop])
            setScrollState([false, true])
        } else {
            setScrollPosition([scrollPosition[1], e.target.scrollTop])
        }

    } else if (upOrDownScroll[1]) {

        ///// set state when user scroll to down
        if (scrollPosition[1] < e.target.scrollTop) {
            setScrollPosition([scrollPosition[1], e.target.scrollTop])
            setScrollState([true, false])
        } else {
            setScrollPosition([scrollPosition[1], e.target.scrollTop])
        }

    }

}