import React, {FC, useEffect} from 'react'
import {AppStateType} from '../../../store/store'
import {connect} from 'react-redux'
import ChooseCategory from './choose-category'
import {
    ChangeMainCategory, ChangeSecondCategory,
    SelectAllMainCategories,
    SelectAllSecondCategories,
    SetShowCategories
} from '../../../store/categories-reducer'

type PropsType = {
    SetShowCategories: () => void
    SelectAllMainCategories: () => void
    SelectAllSecondCategories: () => void
    ChangeMainCategory: (categoryId: number) => void
    ChangeSecondCategory: (categoryId: number) => void

    showCategories: boolean
    mainCategoriesState: Array<boolean>
    secondCategoriesState: Array<boolean>
    local: Array<string>
    upOrDownScroll: Array<boolean>
}

let ChooseCategoryContainer: FC<PropsType> = ({
                                                  showCategories,
                                                  SetShowCategories,
                                                  mainCategoriesState,
                                                  SelectAllMainCategories,
                                                  ChangeMainCategory,
                                                  local,
                                                  upOrDownScroll,
                                                  SelectAllSecondCategories,
                                                  ChangeSecondCategory,
                                                  secondCategoriesState
                                              }) => {
    
    ///// trigger when user scroll page
    useEffect(()=>{
            SetShowCategories()
    },[upOrDownScroll])

    
    let PropsCategories = {
        showCategories,
        mainCategoriesState,
        SelectAllMainCategories,
        ChangeMainCategory,
        local,
        SelectAllSecondCategories,
        ChangeSecondCategory,
        secondCategoriesState
    }
    
        return <ChooseCategory {...PropsCategories} />
    
}

///////////// create props for component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.categories,
        showCategories: state.CategoriesReducer.showCategories,
        mainCategoriesState: state.CategoriesReducer.mainCategoriesState,
        secondCategoriesState: state.CategoriesReducer.secondCategoriesState
    }
}


export default connect(mapStateToProps, {
    SetShowCategories,
    SelectAllMainCategories,
    ChangeMainCategory,
    SelectAllSecondCategories,
    ChangeSecondCategory
})(ChooseCategoryContainer)
