import React, {FC} from 'react'
import styled from 'styled-components'
import preloader from '../../../images/preloader.svg'

type PropsType = {
    full: boolean
    height?: number
}

let Preloader: FC<PropsType> = ({full, height = '150' }) => {

if (full) {
    return <BackgroundPreloader>
        {/*// @ts-ignore*/}
        <PreloaderContainer height={height}>
            <img src={preloader} alt="preloader"/>
        </PreloaderContainer>
    </BackgroundPreloader>
} else {
    // @ts-ignore
    return <PreloaderContainer height={height}>
        <img src={preloader} alt="preloader"/>
    </PreloaderContainer>
}}

let PreloaderContainer = styled.div`

height: ${(props: any) => {return props.height + 'px'}};
img {
width: inherit;
height: inherit;
border-radius: 100%;
}
`

let BackgroundPreloader = styled.div`
height: 100%;
width: 100%;
justify-content: center;
align-items: center;
display: flex;
background: linear-gradient(90deg, #6D4BCE 0%, #4398B6 100%);
`

export default Preloader
