import {Localization} from '../config'

///////////// Const for action creators


///////////// Initial state
type LocalizationInitStateType = {
    currentLangId: typeof Localization.UA.langId,
    langData: typeof Localization.UA.langData
}

let initialState: LocalizationInitStateType = {
    currentLangId: Localization.UA.langId,
    langData: Localization.UA.langData
}

///////////// Reduser
const LocalizationReducer = (state = initialState, action: ActionsTypes): LocalizationInitStateType => {
    switch (action.type) {
        default: {
            return state
        }
    }

}

///////////// Action creators
type ActionsTypes = any

/////////////

export default LocalizationReducer