///////////// Const for action creators
const SET_SHOW_CATEGORIES = 'SET_SHOW_CATEGORIES'
const SELECT_ALL_MAIN_CATEGORIES = 'SELECT_ALL_MAIN_CATEGORIES'
const SELECT_ALL_SECOND_CATEGORIES = 'SELECT_ALL_SECOND_CATEGORIES'
const CHANGE_MAIN_CATEGORY = 'CHANGE_MAIN_CATEGORY'
const CHANGE_SECOND_CATEGORY = 'CHANGE_SECOND_CATEGORY'

///////////// Initial state
type CategoriesInitStateType = {
    showCategories: boolean
    mainCategoriesState: Array<boolean>
    secondCategoriesState: Array<boolean>
}

let initialState: CategoriesInitStateType = {
    showCategories: true,
    mainCategoriesState: [true, false, false, false, false],
    secondCategoriesState: [true, false, false, false, false]
}

///////////// Reduser
const CategoriesReducer = (state = initialState, action: ActionsTypes): CategoriesInitStateType => {
    switch (action.type) {
        
        case SET_SHOW_CATEGORIES: {
            return {
                ...state,
                showCategories: !state.showCategories
            }
        }
        case SELECT_ALL_MAIN_CATEGORIES : {
            return {
                ...state,
                mainCategoriesState: [true, false, false, false, false]
            }
        }
        case SELECT_ALL_SECOND_CATEGORIES : {
            return {
                ...state,
                secondCategoriesState: [true, false, false, false, false]
            }
        }
        case CHANGE_MAIN_CATEGORY: {
            
            ///// set category
            let newCategories = [...state.mainCategoriesState]
            newCategories[action.categoryId] = !newCategories[action.categoryId]
            
            ///// if category is true - set false "all" button
            if (newCategories[action.categoryId]) {
                newCategories[0] = false
            }
            
            ///// if category not choose - set true "all" button
            if (!newCategories.includes(true)) {
                newCategories[0] = true
            }
            
            return {
                ...state,
                mainCategoriesState: newCategories
            }
        }
        case CHANGE_SECOND_CATEGORY: {

            ///// set category
            let newCategories = [...state.secondCategoriesState]
            newCategories[action.categoryId] = !newCategories[action.categoryId]

            ///// if category is true - set false "all" button
            if (newCategories[action.categoryId]) {
                newCategories[0] = false
            }

            ///// if category not choose - set true "all" button
            if (!newCategories.includes(true)) {
                newCategories[0] = true
            }

            return {
                ...state,
                secondCategoriesState: newCategories
            }
        }
        default: {
            return state
        }
    }

}

///////////// Action creators
type ActionsTypes =
    SetShowCategoriesType |
    SelectAllMainCategoriesType |
    ChangeMainCategoryType |
    SelectAllSecondCategoriesType |
    ChangeSecondCategoryType

type SetShowCategoriesType = {
    type: typeof SET_SHOW_CATEGORIES
}
export let SetShowCategories = (): SetShowCategoriesType => ({type: SET_SHOW_CATEGORIES})


type SelectAllMainCategoriesType = {
    type: typeof SELECT_ALL_MAIN_CATEGORIES
}
export let SelectAllMainCategories = (): SelectAllMainCategoriesType => ({type: SELECT_ALL_MAIN_CATEGORIES})

type SelectAllSecondCategoriesType = {
    type: typeof SELECT_ALL_SECOND_CATEGORIES
}
export let SelectAllSecondCategories = (): SelectAllSecondCategoriesType => ({type: SELECT_ALL_SECOND_CATEGORIES})


type ChangeMainCategoryType = {
    type: typeof CHANGE_MAIN_CATEGORY
    categoryId: number
}
export let ChangeMainCategory = (categoryId: number): ChangeMainCategoryType => ({type: CHANGE_MAIN_CATEGORY, categoryId})

type ChangeSecondCategoryType = {
    type: typeof CHANGE_SECOND_CATEGORY
    categoryId: number
}
export let ChangeSecondCategory = (categoryId: number): ChangeSecondCategoryType => ({type: CHANGE_SECOND_CATEGORY, categoryId})


/////////////

export default CategoriesReducer