import React, {FC} from 'react'
import {Page} from '../../app-styled-components'
import {FormBox, InputText, LoadButton, SubmitButton} from './style'
import {Form} from 'react-final-form'
import InputField from '../common-components/fields/input-field'
import {composeValidators, required} from '../../services/validations'
import {AuthDataType} from '../../types/types'
import Preloader from '../common-components/preloader/preloader'
import UploadAvatar from '../common-components/fields/upload-avatar'

///////////// types for props
type PropsType = {
    onSubmit: (formData: AuthDataType) => void
    
    isLoadNewUser: boolean
    local: Array<string>
}


let Auth: FC<PropsType> = ({
                               local,
                               onSubmit, 
                               isLoadNewUser
}) => {

    return <Page>
        <Form onSubmit={onSubmit}
              render={({handleSubmit}) => (
                  <FormBox onSubmit={handleSubmit}>
                      <UploadAvatar/>
                      <InputText
                          validate={composeValidators(required)} 
                          name='name'
                          component={InputField}
                          placeholder={local[2]}
                      />
                      <InputText
                          name='phone'
                          component={InputField}
                          placeholder={local[3]}
                      />
                      
                      {isLoadNewUser
                      ? <LoadButton><Preloader full={false} height={40}/></LoadButton>
                      : <SubmitButton type='submit'>{local[4]}</SubmitButton>}
                  </FormBox>
              )}
        />
    </Page>
}


export default Auth