import React, {FC, useEffect} from 'react'
import {AppStateType} from '../../store/store'
import {connect, ConnectedProps} from 'react-redux'
import Auth from './auth'
import {AddNewUserThunk, CleanState, ToggleLoadSubmit} from '../../store/auth-reducer'
import {AuthDataType} from '../../types/types'
import { Redirect } from 'react-router-dom'


///////////// types for props
type PropsType = PropsFromRedux
// types takes from connect() where added mapStateToProps and mapDispatchToProps
type PropsFromRedux = ConnectedProps<typeof connector>


let AuthContainer: FC<PropsType> = ({
                                        local,
                                        AddNewUserThunk, 
                                        ToggleLoadSubmit, 
                                        isLoadNewUser, 
                                        CleanState,
                                        isRedirectToMainPage
}) => {
    
    useEffect(()=>{
        return ()=>{CleanState()}
    },[])
    
    const onSubmit = (formData: AuthDataType) => {
        AddNewUserThunk(formData)
        ToggleLoadSubmit()
    }
    
    let AuthProps = {
        isLoadNewUser,
        onSubmit,
        local
    }
    
    if (isRedirectToMainPage) {
        return <Redirect to='/' />
    } else {
        return <Auth {...AuthProps}/>
    }
    
}

///////////// create props for App component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.auth_page,
        isLoadNewUser: state.AuthReducer.isLoadNewUser,
        isRedirectToMainPage: state.AuthReducer.isRedirectToMainPage
    }
}

const connector = connect(mapStateToProps, { AddNewUserThunk, ToggleLoadSubmit, CleanState })
export default connector(AuthContainer)
