import React, {FC} from 'react'
import { Redirect } from 'react-router-dom'

type PropsType = {
    component: FC
    auth: boolean | null
}
const PrivateRoute: FC<PropsType> = ({ component: Component, auth}) => {

    return <>
        {auth
            ? <Component />
            : <Redirect to="/login" />}
    </>

}

export default PrivateRoute