import styled from 'styled-components'

export let Container = styled.div`
height: ${(props: any) => {
    return props.height + 'px'
}};
background: linear-gradient(90deg, #6D4BCE 0%, #4398B6 100%);
max-width: 586px;
width: 100%;
display: inline-block;

@media (min-width: 586px) {
display: none;
}
`

export let FullScreenBlock = styled.div`
height: ${(props: any) => {
    return props.height + 'px'
}};
background: linear-gradient(90deg, #6D4BCE 0%, #4398B6 100%);
width: 100%;
display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
h2 {

}

@media (max-width: 586px) {
display: none;
}
`

export let Page = styled.div`
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    align-items: center;
        overflow-y: overlay;
`

export let DefaultButtonStyles = `
width: calc(100% - 83px);
min-width: 250px;
height: 66px;
background: #000000;
border-radius: 49px;
color: #FFFFFF;
font-weight: 800;
font-size: 20px;
text-transform: uppercase;
cursor: pointer;
display: flex;
align-items: center;
justify-content: center;
outline: none;
border: none;
`

export let TextInputStyle = `
background: none;
    outline: none;
    border: 3px solid rgba(0, 0, 0, 0.8);
    border-radius: 49px;
    height: 66px;
    box-sizing: border-box;
    width: calc(100% - 84px);
    min-width: 250px;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 52px;
color: rgba(255, 255, 255, 0.7);
    
    ::placeholder {
font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 52px;
color: rgba(255, 255, 255, 0.7);
text-transform: uppercase;
transition: 0.25s;
    }
   
:focus::placeholder {
color: rgba(255, 255, 255, 0.0);
}
`